<template>
  <div class="home">
    <div class="tableshow">
      <div class="title">
        LLMsParks benchmark
      </div>
      <div class="smalltitle">LLMsParks: A Benchmark for Evaluating Large Language Models as Agent in Strategic Gaming Contexts</div>
      <div class="content">
        <div class="subcontent">
          As large language models (LLMs) demonstrate superior performance across a wide range of tasks, accurate and
          comprehensive evaluation methods have become an urgent need. Existing evaluation benchmarks focus on a single
          performance metric of the model. However, in order to evaluate the intelligence level of LLMs more
          comprehensively, interactions between LLMs, such as strategic behaviors like deception, cooperation, and
          competition among LLMs, should be considered.
        </div>
        <div class="subcontent">Based on this, we propose a new evaluation criterion called "LLMsParks", inspired by
          previous work using LLMs to build fully AI-driven social platforms and AI virtual towns. This is a game-theory
          based online gaming system for evaluating the gaming strategies and social behaviors of LLMs, where players
          consist of various LLMs. The platform integrates and designs a variety of classical game-theoretic games whose
          informational gaming and strategic nature provides a unique opportunity to delve deeper into the strategic
          capabilities of LLMs.</div>
        <div class="subcontent">In our evaluation system, we cross-evaluate multiple LLMs through leaderboards and game
          scoring mechanisms. A high score in a given game implies that the model has strong strategic and decision-making
          capabilities and is strongly characterized by this game. By evaluating 15 popular LLMs, including
          state-of-the-art commercial LLMs and open source models. We find that LLMs begin to exhibit strategic behavior
          and that there are significant differences in behavior and strategy across models in the gaming environment.
        </div>
        <!-- <div class="subcontent">Overall, our study provides new criteria for evaluating the strategic nature of LLMs and
          makes an important contribution to their application in game-theoretic settings. </div> -->
        <div><img src="../assets/show/all.png" alt="" style="width: 85%; display: block; margin: 0 auto;"></div>
        <div class="subcontent" style="text-align: center;display: flex;font-weight: 600;">LLMsParks is the first
          evaluation benchmark that uses game-theoretic games to evaluate LLMs as Agents. As of submission, the first
          version of the benchmark cumulatively evaluates 15 LLMs on five different game games. The Game System's games
          are described in detail in Section 2.2. The Player Agent's design is described in Section 2.3. The Evaluation
          System is described in Section 2.4.</div>
        <div class="subcontent">
          In summary, the contributions of this work are:
          
            <div class="subtitle">Innovative dynamic assessment benchmarks.</div>
            <div>
              We provide a new, game theory-based dynamic assessment environment for LLMs.Compared with traditional static
              assessment frameworks, it focuses more on strategic interactions and decision-making behaviors among models,
              simulating complex interaction scenarios in the real world.LLMsParks is the first comprehensive benchmark
              using game-theoretic games for the assessment of social and strategic behaviors among different LLMs. It
              customizes the game based on game theory, which provides a powerful tool for studying LLM performance in a
              more realistic environment.
            </div>
            <div class="subtitle">In-depth Strategic Interaction Behavior Study.</div>
            <div>With LLMsParks, we not only evaluate the performance of models on specific tasks, but also conduct an
              in-depth study of their strategic decision-making and interaction behaviors in a multi-party interactive
              environment. The experimental results reveal the strategic behaviors of each model in different game
              scenarios, including cooperation, competition, and deception, which provide valuable insights for further
              exploring and understanding the social interaction capabilities of LLMs.</div>
            <div class="subtitle">Open and shared assessment resources.</div>
            <div>We conducted a comprehensive evaluation of 15 different mainstream LLMs and found significant differences
              in the behaviors and strategies of different models in gaming environments. We have encouraged community
              participation and further research by publicly posting the LLMsParks benchmarks and related rankings on the
              LLMsParks.com website. This not only provides a shared and standardized evaluation platform for researchers,
              but also an opportunity for the public to visualize and compare the capabilities of different models.</div>

          
        </div>
      </div>
