            <div class="title">
                Who Is Spy
            </div>
            <div class="content">
                <div class="subtitle">
                    Game Rules
                </div>
                <div class="subcontent">
                    Through descriptions, reasoning and voting among the players, the "undercover agent" hidden among the
                    players is identified. At the beginning of each round, two words are randomly assigned to players. One
                    player is given a different vocabulary than the others - this player is the "undercover agent". When it
                    is each player's turn, they must describe the vocabulary word they have received, but not directly say
                    the word itself. The description should be as vague as possible to prevent the undercover agent from
                    easily guessing the correct word, but not so far off the mark as to be mistaken for an undercover agent.
                    After all players have finished their descriptions, the whole group discusses them. Players may ask
                    other players questions about their descriptions in order to reason out who might be an undercover
                    agent. At the end of the discussion, each player votes for the player they think is undercover. The
                    player with the most votes is considered undercover and eliminated. If the eliminated player is indeed
                    undercover, the civilian player wins. If the eliminated player is not undercover, the game continues to
                    the next round.
                </div>
                <div class="subcontent">
                    The game ends when the undercover agent is identified or when the undercover agent succeeds in confusing
                    the other players and survives to the last round.
                </div>
                <div class="subtitle">
                    Requirements
                </div>
                <div class="subcontent">
                    Players must remain honest when describing vocabulary and must not intentionally provide misinformation.
                    Undercover players should try to ensure that their descriptions are as similar as possible to those of
                    other players to avoid easy identification.
                </div>
                <div class="subcontent">
                    <li>Features: This is a social game of strategy and psychoanalysis that requires players to cheat and
                        reason.</li>
                    <li>Demonstration of modelling capabilities: In the game, LLM has to demonstrate not only its text
                        generation and implicit information retrieval capabilities, but also its contextual reasoning
                        capabilities.</li>
                    <li>Highlight: If the LLM can avoid detection for long periods of time as an undercover agent, or can
                        accurately recognise an undercover agent as a civilian, then it has probably mastered advanced
                        social interaction and psychoanalysis skills.</li>
                </div>
