<template>
    <div class="home">
        <div class="tableshow">
            <div class="title">
                The Prisoner's Dilemma
            </div>
            <div class="content">
                <div class="subtitle">
                    Game Rules
                </div>
                <div class="subcontent">
                    The Prisoner's Dilemma is a classic problem that describes the interaction between two prisoners.
                    Suppose the police catch two suspects in the act of committing a crime, but there is not enough evidence
                    to charge them with a felony. Each suspect has two choices:cooperate or betray the other. Different
                    choices on both sides result in outcomes.
                </div>
                <div class="subcontent">
                    The following are possible outcomes:<br>
                    <ol>
                        <li>1. If both A and B choose to cooperate (i.e., remain silent), they will both be sentenced to
                            lesser penalties.</li>
                        <li>2. If A chooses to betray B but B chooses to cooperate, then A will be released and B will be
                            convicted of a felony.</li>
                        <li>3. If B chooses to betray A but A chooses to cooperate, then B will be released and A will be
                            convicted of a felony.</li>
                        <li>4. If both A and B choose to betray the other, then they will both be sentenced to moderate
                            penalties.</li>
                    </ol>
                </div>
                <div class="subcontent">
                    Define the following incentives (or penalties):<br>
                    <ol>
                        <li>R: Reward for cooperation by both parties.</li>
                        <li>T: Reward for Betrayal and Cooperation (Temptation).</li>
                        <li>S: The "punishment" (loss) for cooperating and being betrayed by the other party.</li>
                        <li>P: The penalty for betrayal on both sides.</li>
                    </ol>
                </div>
                <div class="subcontent">
                    Typically, these rewards/punishments satisfy the following relationship: T > R > P > S. Given the
                    strategies of A and B: Let x be the strategy of A, where x = 0 for cooperation and x = 1 for betrayal
                    Let y be the strategy of B, where y = 0 for cooperation and y = 1 for betrayal. Then the payoffs of A
                    and B can be expressed as follows.
                </div>
                <div>$$\begin{array}{l}
                    \text { payoff }(A)=x(1-y) T+(1-x)(1-y) R+x y P+(1-x) y S \\
                    \text { payoff }(B)=y(1-x) T+(1-y)(1-x) R+y x P+(1-y) x S
                    \end{array}$$</div>
                <div class="subcontent">
                    This is the basic form of the Prisoner's Dilemma. The strategy of the game usually depends on the
                    estimation of future gains, which can lead to a variety of cooperative and betrayal strategies.
                </div>
                <div class="subcontent">
                    <li>Features: This game involves interactive decision-making between two players, with a focus on
                        co-operation and betrayal.</li>
                    <li>Demonstration of model capabilities: During the game, the LLM demonstrates its predictive
                        capabilities by trying to anticipate its opponent's choices to make the optimal choice. In addition,
                        LLM's long and short-term memory is especially important in the multi-round version, as it needs to
                        consider the decisions of previous rounds to adjust its current strategy.</li>
                    <li>Highlight: If the LLM shows a high degree of cooperation in the game, this may indicate that it is
                        able to learn the importance of cooperation for long-term gains, in contrast to the single-objective
                        optimisation strategies characterised by the machine.</li>
                </div>
                <div><img src="../assets/show/pd.png" alt="" style="width: 40%; display: block; margin: 0 auto;"></div>
                <div class="subcontent" style="text-align: center;font-weight: 600;">The outcome of a Prisoner's Dilemma
                    game in which the parties choose to cooperate or betray.</div>
