<template>
    <div class="home">
        <div class="tableshow">
            <div class="title">
                The Nim Game
            </div>
            <div class="content">
                <div class="subtitle">
                    Game Rules
                </div>
                <div class="subcontent">
                    The game starts with several piles of stones, each pile can have a different number of stones. Two players take turns. On each player's turn, a player must take at least one stone from a pile; they can take as many stones as they like but all stones must come from the same pile, and the player who can't take any more stones loses the game (i.e., the player who takes the last stone wins the game). The key to the Nim game is the Nim sum (Nim-sum). For a given pile of stones, the Nim-sum number is computed as the binary isomorphism of the number of stones in all piles:.
                </div>
                <div> $$\text { Nim-sum }=S_{1} \oplus S_{2} \oplus \cdots \oplus S_{n}$$ </div>
                <div class="subcontent">
                    where "⊕" denotes a binary different or. If the Nim sum is 0 at the start of the game, then the first player does not have a winning strategy (assuming both use the best strategy). If the Nim sum is not zero, then the first player has a winning strategy. This is because, for non-zero Nim-sums, the first player can always make the Nim-sum 0 by choosing the right pile and removing the right number of stones, and then, no matter how much the second player moves, the first player can always keep the Nim-sum 0 until the end of the game. When the Nim sum is 0, the late player is at such a disadvantage. Thus, the Nim sum number provides an easy way to determine which player has the advantage and provides them with a clear strategy to win the game.
                </div>
                <div><img src="../assets/show/nim.png" alt="" style="width: 65%; display: block; margin: 0 auto;"></div>
                <div class="subcontent" style="text-align: center;font-weight: 600;">A display of one of the games in our implementation of the Nim Game.</div>
            </div>
