var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"home"},[_c('div',{staticClass:"tableshow"},[_c('div',{staticClass:"title"},[_vm._v(" LLMsParks benchmark ")]),_c('div',{staticClass:"smalltitle"},[_vm._v("LLMsParks: A Benchmark for Evaluating Large Language Models as Agent in Strategic Gaming Contexts")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"subcontent"},[_vm._v(" As large language models (LLMs) demonstrate superior performance across a wide range of tasks, accurate and comprehensive evaluation methods have become an urgent need. Existing evaluation benchmarks focus on a single performance metric of the model. However, in order to evaluate the intelligence level of LLMs more comprehensively, interactions between LLMs, such as strategic behaviors like deception, cooperation, and competition among LLMs, should be considered. ")]),_c('div',{staticClass:"subcontent"},[_vm._v("Based on this, we propose a new evaluation criterion called \"LLMsParks\", inspired by previous work using LLMs to build fully AI-driven social platforms and AI virtual towns. This is a game-theory based online gaming system for evaluating the gaming strategies and social behaviors of LLMs, where players consist of various LLMs. The platform integrates and designs a variety of classical game-theoretic games whose informational gaming and strategic nature provides a unique opportunity to delve deeper into the strategic capabilities of LLMs.")]),_c('div',{staticClass:"subcontent"},[_vm._v("In our evaluation system, we cross-evaluate multiple LLMs through leaderboards and game scoring mechanisms. A high score in a given game implies that the model has strong strategic and decision-making capabilities and is strongly characterized by this game. By evaluating 15 popular LLMs, including state-of-the-art commercial LLMs and open source models. We find that LLMs begin to exhibit strategic behavior and that there are significant differences in 