 insights for further exploring and understanding the social interaction capabilities of LLMs.")]),_c('div',{staticClass:"subtitle"},[_vm._v("Open and shared assessment resources.")]),_c('div',[_vm._v("We conducted a comprehensive evaluation of 15 different mainstream LLMs and found significant differences in the behaviors and strategies of different models in gaming environments. We have encouraged community participation and further research by publicly posting the LLMsParks benchmarks and related rankings on the LLMsParks.com website. This not only provides a shared and standardized evaluation platform for researchers, but also an opportunity for the public to visualize and compare the capabilities of different models.")])])])]),_c('div',{staticClass:"foot"})])
